import React from "react";
import { blogs } from "../constants";

const BlogPreview = () => {
  const isUpdating = true; // Toggle to false when blogs are available

  return (
    <div className="blog-preview max-h-64 overflow-y-scroll">
      <div className="space-y-4 p-4">
        {isUpdating ? (
          <div className="p-8 bg-gray-100 border border-gray-300 rounded-lg text-center">
            <h3 className="text-2xl font-semibold text-gray-700">Coming Soon</h3>
            <p className="mt-2 text-gray-600">
              Stay tuned! Our blog content will be available shortly.
            </p>
          </div>
        ) : (
          blogs.map((blog) => (
            <div
              key={blog.id}
              className="p-4 bg-white shadow-md rounded-md hover:bg-gray-100 hover:shadow-lg transition-all duration-300"
            >
              <h3 className="text-xl font-semibold">{blog.title}</h3>
              <p className="text-gray-500 text-sm">{blog.date}</p>
              <p className="mt-4 text-gray-600">{blog.summary}</p>
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default BlogPreview;
